<template>
  <v-dialog fullscreen v-model="showDialog">
    <v-card>
      <v-card-title>
        <H2>D&D</H2>
      </v-card-title>
      <v-card-text>
        <p>D&D5</p>
        <h3></h3>
        <v-expansion-panel>
          <v-expansion-panel-content>
            <div slot="header">
              <h4>1.</h4>
            </div>
            <v-card>
              <v-card-text>Open SRD </v-card-text>
              <v-card-text>PHB</v-card-text>
              <v-card-text></v-card-text>
            </v-card>
          </v-expansion-panel-content>
        </v-expansion-panel>
        <h3></h3>
        <v-expansion-panel>
          <v-expansion-panel-content>
            <div slot="header">
              <h4>1.</h4>
            </div>
            <v-card>
              <v-card-text><img src="../assets/upload.png"></v-card-text>
              <v-card-text><img src="../assets/save.png"></v-card-text>
              <v-card-text><img src="../assets/download.png"></v-card-text>
              <v-card-text><img src="../assets/reload.png"></v-card-text>
              <v-card-text><img src="../assets/discard.png"></v-card-text>
              <v-card-text><img src="../assets/add.png"></v-card-text>
              <v-card-text><img src="../assets/help.png"></v-card-text>
            </v-card>
          </v-expansion-panel-content>
        </v-expansion-panel>
        <v-expansion-panel>
          <v-expansion-panel-content>
            <div slot="header">
              <h4>2.</h4>
            </div>
            <v-card>
              <v-card-text>TODO</v-card-text>
            </v-card>
          </v-expansion-panel-content>
        </v-expansion-panel>
        <v-expansion-panel>
          <v-expansion-panel-content>
            <div slot="header">
              <h4>3.</h4>
            </div>
            <v-card>
              <v-card-text>TODO</v-card-text>
            </v-card>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="green darken-1" flat @click.native="closeDialog"></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<style>

</style>

<script>
export default {
  name: "SpellHelpDialog",
  props: {
    //
    showDialog: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    closeDialog() {
      this.$emit("update:showDialog", false);
    }
  }
};
</script>



