<template>
  <div>
    <!---->
    <v-dialog v-model="showEditDialog" persistent v-if="editmode">
      <v-card color="grey lighten-4" flat>
        <v-card-text>
          <v-container fluid>
            <v-form ref="form">
              <v-layout row wrap>
                <v-flex md4>
                  <v-text-field name="" type="text" label="" v-model="spell.name" required/>
                </v-flex>
                <v-flex xs3>
                  <v-select label="Select" :items="classes" v-model="spell.class" multiple max-height="400" hint="" persistent-hint></v-select>
                </v-flex>
                <v-flex xs3>
                  <v-select label="Select" :items="levels" v-model="spell.level" max-height="400" hint="" persistent-hint></v-select>
                </v-flex>
                <v-flex xs3>
                  <v-select label="Select" :items="components" v-model="spell.components" multiple max-height="400" hint="" persistent-hint></v-select>
                </v-flex>
                <v-flex xs3>
                  <v-text-field name="" type="text" label="" v-model="spell.materialdeteil" />
                </v-flex>
                <v-flex xs3>
                  <v-text-field name="" label="" v-model="spell.casting_time" required />
                </v-flex>
                <v-flex xs3>
                  <v-text-field name="" label="" v-model="spell.duration" required />
                </v-flex>
                <v-flex xs2>
                  <v-select label="Select" :items="concentration" v-model="spell.concentration" max-height="400" hint="" persistent-hint></v-select>
                </v-flex>
                <v-flex xs2>
                  <v-text-field name="" label="" v-model="spell.range" required />
                </v-flex>
                <v-flex xs2>
                  <v-select label="Select" :items="rituals" v-model="spell.ritual" max-height="400" hint="" persistent-hint></v-select>
                </v-flex>
                <v-flex xs12>
                  <v-text-field class="nowrap" name="" label="" v-model="spell.desc" multi-line required />
                </v-flex>
              </v-layout>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click.native="save"></v-btn>
          <v-btn color="green darken-1" flat @click.native="cancel"></v-btn>
          <v-btn color="green darken-1" flat @click.native="remove"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!---->
    <v-dialog v-model="showEditDialog" persistent v-if="editmode === false">
      <!---->
      <v-card color="grey lighten-4" flat class="hidden-sm-and-down">
        <v-card-title>
          <h2>{{spell.name}}</h2>
        </v-card-title>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs3>
            <v-subheader class="body-2"></v-subheader>
          </v-flex>
          <v-flex xs3>
            <v-subheader class="body-2"></v-subheader>
          </v-flex>
          <v-flex xs3>
            <v-subheader class="body-2"></v-subheader>
          </v-flex>
          <v-flex xs3>
            <v-subheader class="body-2"></v-subheader>
          </v-flex>
          <v-flex xs3>
            <v-card-text class="body-2">{{ spell.formatArray(spell.class,classes)}}</v-card-text>
          </v-flex>
          <v-flex xs3>
            <v-card-text class="body-2">{{ spell.level }}</v-card-text>
          </v-flex>
          <v-flex xs3>
            <v-card-text class="body-2">{{formatComponents(spell)}}</v-card-text>
          </v-flex>
          <v-flex xs3>
            <v-card-text class="body-2">{{ spell.casting_time }}</v-card-text>
          </v-flex>
          <v-flex xs3>
            <v-subheader class="body-2"></v-subheader>
          </v-flex>
          <v-flex xs3>
            <v-subheader class="body-2"></v-subheader>
          </v-flex>
          <v-flex xs3>
            <v-subheader class="body-2"></v-subheader>
          </v-flex>
          <v-flex xs3>
          </v-flex>
          <v-flex xs3>
            <v-card-text class="body-2">{{ spell.formatDuration }}</v-card-text>
          </v-flex>
          <v-flex xs3>
            <v-card-text class="body-2">{{ spell.range }}</v-card-text>
          </v-flex>
          <v-flex xs3>
            <v-card-text class="body-2">{{ spell.format(spell.ritual,rituals) }}</v-card-text>
          </v-flex>
          <v-flex xs3>
          </v-flex>
          <v-flex xs12>
            <v-divider/>
            <v-card-text class="body-2">
              <span style="white-space: pre-wrap;" v-html="formatDesc(spell)"></span>
            </v-card-text>
          </v-flex>
        </v-layout>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click.native="close"></v-btn>
          <v-btn color="green darken-1" flat @click.native="edit"></v-btn>
        </v-card-actions>
      </v-card>

      <!---->
      <v-card class="hidden-md-and-up">
        <v-card-title>
          <h2>{{spell.name}}</h2>
        </v-card-title>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs6>
            <v-list two-line>
              <v-list-tile avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="body-1"></v-list-tile-title>
                  <v-list-tile-content class="body-1">{{spell.level}}</v-list-tile-content>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider/>
              <v-list-tile avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="body-1"></v-list-tile-title>
                  <v-list-tile-content class="body-1">{{spell.range}}</v-list-tile-content>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider/>
              <v-list-tile avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="body-1"></v-list-tile-title>
                  <v-list-tile-content class="body-1">{{spell.formatDuration}}</v-list-tile-content>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider/>
            </v-list>
          </v-flex>
          <v-flex xs6>
            <v-list two-line>
              <v-list-tile avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="body-1"></v-list-tile-title>
                  <v-list-tile-content class="body-1">{{spell.casting_time}}</v-list-tile-content>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider/>
              <v-list-tile avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="body-1"></v-list-tile-title>
                  <v-list-tile-content class="body-1">{{formatComponents(spell)}}</v-list-tile-content>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider/>
              <v-list-tile avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="body-1"></v-list-tile-title>
                  <v-list-tile-content class="body-1">{{spell.format(spell.ritual,rituals)}}</v-list-tile-content>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider/>
            </v-list>
          </v-flex>
          <v-flex xs12>
            <v-card-text class="body-2 scroll-y">
              <span style="white-space: pre-wrap;" v-html="formatDescMobile(spell)"></span><br/>
            </v-card-text>
          </v-flex>
        </v-layout>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click.native="close"></v-btn>
          <v-btn color="green darken-1" flat @click.native="edit"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<style>

</style>

<script>
import constants from "@/model/constants";
import Spell from "@/model/spell";

export default {
  name: "SpellDetailDialog",
  props: {
    //
    showEditDialog: {
      type: Boolean,
      default: false
    },
    //null
    targetSpell: {
      type: Object,
      default: null
    },
    //
    createSpell: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      //
      editmode: false,
      //
      spell: this.defaultData(),
      classes: constants.classes,
      components: constants.components,
      concentration: constants.concentration,
      levels: constants.levels,
      rituals: constants.rituals,
      schools: constants.schools
    };
  },
  watch: {
    //
    //v-bindSpellSpell
    targetSpell: function(val) {
      if (val != null) {
        this.spell = Spell.assign(val);
      } else {
        this.spell = this.defaultData();
      }
    },
    //v-bind
    createSpell: function(val) {
      this.editmode = val;
    }
  },
  methods: {
    //
    edit() {
      this.editmode = true;
    },
    //
    close() {
      this.$emit("update:showEditDialog", false);
      this.editmode = false;
      this.$emit("close", this.spell);
      this.spell = this.defaultData();
    },
    //
    save() {
      this.$emit("update:showEditDialog", false);
      this.editmode = false;
      this.$emit("save", this.spell);
      this.spell = this.defaultData();
    },
    //
    cancel() {
      this.$emit("update:showEditDialog", false);
      this.editmode = false;
      this.$emit("cancel", this.spell);
      this.spell = this.defaultData();
    },
    //
    remove() {
      this.$emit("update:showEditDialog", false);
      this.editmode = false;
      this.$emit("remove", this.spell);
      this.spell = this.defaultData();
    },
    //
    defaultData() {
      return new Spell();
    },

    formatComponents(spell) {
      var retVal = spell.formatArray(spell.components, this.components);
      if (spell.materialdeteil != "") {
        retVal += " * ";
      }
      return retVal;
    },

    formatDesc(spell) {
      var retVal = spell.desc;
      if (spell.materialdeteil != "") {
        retVal += "\r\n";
        retVal += "\r\n";
        retVal += "(*)";
        retVal += spell.materialdeteil;
      }
      return retVal;
    },

    formatDescMobile(spell) {
      var retVal = this.formatDesc(spell);
      retVal += "\r\n";
      retVal += "\r\n";
      retVal += "";
      retVal += spell.formatArray(spell.class, this.classes);
      return retVal;
    }
  }
};
</script>



