<template>
  <div>
    <v-tabs color="blue lighten-2" dark slider-color="yellow">
      <v-tab v-for="encounter in scenario.encounters" :key="scenario.encounters.indexOf(encounter)" ripple>
        {{encounter.name}}
      </v-tab>

      <v-tab-item v-for="encounter in scenario.encounters" :key="scenario.encounters.indexOf(encounter)">
        <!---->
        <v-card v-if="editMode" flat>
          <v-data-table :headers="edit_headers" :items="encounter.monsters" hide-actions class="elevation-1">
            <template slot="items" slot-scope="props">
              <td class="text-xs-left">
                <v-checkbox primary hide-details v-model="props.item.selected"></v-checkbox>
              </td>
              <td class="text-xs-left">
                <v-text-field v-model="props.item.name" required></v-text-field>
              </td>
              <td class="text-xs-left">
                <v-text-field v-model.number="props.item.maxhp" type="number" required></v-text-field>
              </td>
            </template>
          </v-data-table>
          <div class="text-xs-center mt-3">
            <v-btn @click.native="openMonsterDialog(encounter)"></v-btn>
            <v-btn @click.native="deleteMonsters(encounter)"></v-btn>
            <v-btn @click.native="copyMonster(encounter)"></v-btn>
            <v-btn @click.native="deleteEncounter(encounter)"></v-btn>
          </div>
        </v-card>
        <!---->
        <v-card v-if="!editMode" flat>
          <v-data-table :headers="view_headers" :items="encounter.monsters" hide-actions class="elevation-1">
            <template slot="items" slot-scope="props">
              <td :class="classOf(props.item,'text-xs-left')">
                <v-checkbox primary hide-details v-model="props.item.selected"></v-checkbox>
              </td>
              <td :class="classOf(props.item,'text-xs-left')">
                {{ props.item.name }}</td>
              <td :class="classOf(props.item,'text-xs-left')">
                <v-text-field v-model.number="props.item.damage" type="number"></v-text-field>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-tab-item>
    </v-tabs>

    <div class="text-xs-center mt-3">
      <v-btn v-if="editMode" @click.native="addEncounterDialog=true"></v-btn>
      <v-btn v-if="editMode" @click.native="saveScenalio()"></v-btn>
      <v-btn v-if="editMode" @click.native="cancelScenalio()"></v-btn>
      <v-btn @click.native="editMode=!editMode">{{!editMode ? '' : ''}}</v-btn>
    </div>

    <!---->
    <v-dialog v-model="addEncounterDialog" persistent max-width="480">
      <v-card color="grey lighten-4" flat>
        <v-card-text>
          <v-container fluid>
            <v-form ref="form">
              <v-text-field name="" label="" v-model="editedEncounter.name" required :rules="encounterNameRule" />
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click.native="saveEncounter"></v-btn>
          <v-btn color="green darken-1" flat @click.native="cancelEncounter"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!---->
    <v-dialog v-model="addMonsterDialog" persistent max-width="480">
      <v-card color="grey lighten-4" flat>
        <v-card-text>
          <v-container fluid>
            <v-form ref="form">
              <v-text-field name="" label="" v-model="editedMonster.name" required :rules="monsterNameRule" />
              <v-text-field name="HP" label="HP" v-model.number="editedMonster.maxhp" type="number" required :rules="maxHpRule" />
              <v-text-field name="" label="" v-model.number="addMonsterCount" type="number" required :rules="addMonsterCountRule" />
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click.native="addMonster"></v-btn>
          <v-btn color="green darken-1" flat @click.native="cancelMonster"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>

</template>

<style>

</style>

<script>
export default {
  props: {
    targetScenario: Object,
    default: {
      encounters: []
    }
    // required: true
  },
  data() {
    return {
      name: "HitPointBoard",
      editMode: false,
      addEncounterDialog: false,
      addMonsterDialog: false,
      addMonsterCount: 1,
      editedMonster: {
        name: "",
        damage: 0,
        maxhp: 1,
        selected: false
      },
      editedEncounter: {
        name: "x",
        monsters: []
      },
      selected: [],
      view_headers: [
        {
          text: "",
          value: "select"
        },
        {
          text: "",
          value: "name"
        },
        {
          text: "",
          value: "damage"
        }
      ],
      edit_headers: [
        {
          text: "",
          value: "select"
        },
        {
          text: "",
          value: "name"
        },
        {
          text: "HP",
          value: "maxhp"
        }
      ],
      //deepcopy
      //see https://developer.mozilla.org/ja/docs/Web/JavaScript/Reference/Global_Objects/Object/assign
      scenario: JSON.parse(JSON.stringify(this.targetScenario)),
      //validation
      encounterNameRule: [v => v !== "" || ""],
      monsterNameRule: [v => v !== "" || ""],
      maxHpRule: [
        v => v !== "" || "HP", // .numberString
        v => !isNaN(v) || "HP",
        v => v > 0 || "HP1"
      ],
      addMonsterCountRule: [
        v => v !== "" || "", // .numberString
        v => !isNaN(v) || "",
        v => v > 0 || "1"
      ]
    };
  },
  computed: {},
  methods: {
    deleteEncounter(encounter) {
      this.scenario.encounters.splice(
        this.scenario.encounters.indexOf(encounter),
        1
      );
    },
    saveEncounter() {
      //FIXME 
      //      FORMthis.$refs.form.validate()
      if (!this.$refs.form.validate()) {
        // 
      } else {
        this.addEncounterDialog = false;
        this.scenario.encounters.push(Object.assign(this.editedEncounter));
        this._clear();
      }
    },
    cancelEncounter() {
      this.addEncounterDialog = false;
      this._clear();
    },
    openMonsterDialog(encounter) {
      this.editedEncounter = encounter;
      this.addMonsterDialog = true;
    },
    addMonster() {
      if (!this.$refs.form.validate()) {
        // 
      } else {
        for (let i = 0; i < this.addMonsterCount; i++) {
          let monster = JSON.parse(JSON.stringify(this.editedMonster));
          monster.name = monster.name + (i + 1);
          this.editedEncounter.monsters.push(monster);
        }
        this.addMonsterDialog = false;
        this._clear();
      }
    },
    cancelMonster() {
      this.addMonsterDialog = false;
      this._clear();
    },
    copyMonster(encounter) {
      let copiedMonsters = encounter.monsters.filter(monster => {
        return monster.selected === true;
      });
      copiedMonsters.forEach(element => {
        //
        //see https://developer.mozilla.org/ja/docs/Web/JavaScript/Reference/Global_Objects/Object/assign
        let copied = JSON.parse(JSON.stringify(element));
        copied.selected = false;
        encounter.monsters.push(copied);
      });
    },
    deleteMonsters(encounter) {
      let copiedMonsters = encounter.monsters.filter(monster => {
        return monster.selected === true;
      });
      copiedMonsters.forEach(element => {
        let index = encounter.monsters.indexOf(element);
        encounter.monsters.splice(index, 1);
      });
    },
    //TODO
    saveScenalio() {
      this.$emit("save", this.scenario);
    },
    cancelScenalio() {
      this.$emit("cancel", this.scenario);
    },
    _clear() {
      this.editedEncounter = {
        name: "",
        monsters: []
      };
      this.editedMonster = {
        name: "",
        damage: 0,
        maxhp: 1,
        selected: false
      };
      this.addMonsterCount = 1;
    },
    classOf(monster, classes) {
      if (monster.damage >= monster.maxhp) {
        return classes + " black";
      } else if (monster.damage * 2 >= monster.maxhp) {
        return classes + " red";
      } else {
        return classes;
      }
    }
  }
};
</script>


