<template>
  <div>
    <v-container fluid grid-list-md>
      <v-card>
        <v-toolbar dense>
          <v-toolbar-title></v-toolbar-title>
          <v-spacer></v-spacer>
          <file-upload-icon tooltip="" @onFileRead="onFileRead" />
          <v-tooltip top>
            <v-btn icon small slot="activator" @click="saveSpellData">
              <v-icon>save</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
          <file-download-icon tooltip="" :data="jesonspelldata" />
          <v-tooltip top>
            <v-btn icon small slot="activator" @click="loadSpellData">
              <v-icon>redo</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
          <v-tooltip top>
            <v-btn icon small slot="activator" @click="deleteSpellData">
              <v-icon>delete</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
          <v-tooltip top>
            <v-btn icon small slot="activator" @click="addSpell">
              <v-icon>add</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
          <v-tooltip top>
            <v-btn icon small slot="activator" @click="showHelp">
              <v-icon>help</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
        </v-toolbar>

        <!---->
        <!---->
        <v-layout v-if="!isMobile" row wrap justify-center>
          <v-flex xs1>
          </v-flex>
          <v-flex xs10>
            <v-text-field append-icon="search" row-height="12" label="Input" single-line v-model="conditon.spellname" hint="" persistent-hint></v-text-field>
          </v-flex>
          <v-flex xs1>
          </v-flex>
          <v-flex xs1>
          </v-flex>
          <v-flex xs2>
            <v-select label="Select" :items="levels" v-model="conditon.levels" multiple max-height="400" hint="" persistent-hint></v-select>
          </v-flex>
          <v-flex xs2>
            <v-select label="Select" :items="classes" v-model="conditon.classes" multiple max-height="400" hint="" persistent-hint></v-select>
          </v-flex>
          <v-flex xs2>
            <v-select :items="conditonRituals" v-model="conditon.ritual" max-height="400" hint="" persistent-hint></v-select>
          </v-flex>
          <v-flex xs2>
            <v-select label="Select" :items="components" v-model="conditon.components" multiple max-height="400" hint="" persistent-hint></v-select>
          </v-flex>
          <v-flex xs2>
            <v-select label="Select" :items="casting_time" v-model="conditon.casting_time" max-height="400" hint="" persistent-hint></v-select>
          </v-flex>
          <v-flex xs1>
          </v-flex>
        </v-layout>
        <!---->
        <v-data-table v-if="!isMobile" :headers="headers" :items="items" item-key="name" no-data-text="">
          <template slot="items" slot-scope="props">
            <tr @click="clickCell(props.item)">
              <td class="text-xs-left">{{ formatSpellName(props.item) }} </td>
              <td class="text-xs-left">{{ props.item.hoge }} {{ props.item.level}}</td>
              <td class="text-xs-left">{{ props.item.formatArray(props.item.components,components) }}</td>
              <td class="text-xs-left nowrap">{{ props.item.casting_time }}</td>
              <td class="text-xs-left">{{ props.item.formatDuration }}</td>
              <td class="text-xs-left">{{ props.item.range }}</td>
              <td class="text-xs-left">
                <v-icon>add</v-icon>
              </td>
            </tr>
          </template>
        </v-data-table>
        <!---->

        <!---->
        <!---->
        <v-layout v-if="isMobile" row wrap>
          <v-flex xs10 offset-xs1>
            <v-text-field append-icon="search" label="Input" single-line v-model="conditon.spellname" hint="" persistent-hint></v-text-field>
          </v-flex>
          <v-flex xs6 offset-xs3>
            <v-btn @click="showMobileSearchDetailDialog=true">
              <v-icon>settings</v-icon> 
            </v-btn>
          </v-flex>
        </v-layout>
        <!---->
        <v-list v-if="isMobile" dense two-line>
          <v-data-iterator content-tag="v-card" :items="items">
            <v-list-tile avatar slot="item" slot-scope="props" @click="clickCell(props.item)">
              <v-list-tile-content>
                <v-list-tile-title>{{props.item.name}} </v-list-tile-title>
                <v-list-tile-sub-title>{{props.item.level}}/{{props.item.format(props.item.school,schools)}}/{{props.item.formatArray(props.item.components,components)}}</v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-avatar>
                <v-icon>add</v-icon>
              </v-list-tile-avatar>
            </v-list-tile>
          </v-data-iterator>
        </v-list>

        <!---->

      </v-card>
      <v-card>
        <ins class="adsbygoogle" style="display:block" data-ad-client="ca-pub-9097509632200457" data-ad-slot="5985300299" data-ad-format="auto"></ins>
      </v-card>
    </v-container>
    <v-snackbar top :color="snackbar.level" :timeout="3000" v-model="snackbar.show">
      {{ snackbar.message }}
    </v-snackbar>
    <spell-detail-dialog :showEditDialog.sync="showEditDialog" v-bind:targetSpell="targetSpell" v-bind:createSpell="createSpell" @save="save" @remove="remove" @cancel="cancelOrClose" @close="cancelOrClose" />
    <spell-help-dialog :showDialog.sync="showHelpDialog"></spell-help-dialog>

    <v-dialog v-if="isMobile" fullscreen v-model="showMobileSearchDetailDialog">
      <v-card>
        <v-card-title>
          <H2></H2>
        </v-card-title>
        <v-card>
          <v-layout row wrap>
            <v-flex xs10 offset-xs1>
              <v-select label="Select" hide-selected dense :items="levels" v-model="conditon.levels" multiple max-height="200" hint="" persistent-hint></v-select>
            </v-flex>
            <v-flex xs10 offset-xs1>
              <v-select label="Select" hide-selected dense :items="classes" v-model="conditon.classes" multiple max-height="200" hint="" persistent-hint></v-select>
            </v-flex>
            <v-flex xs10 offset-xs1>
              <v-select :items="conditonRituals" dense v-model="conditon.ritual" max-height="200" hint="" persistent-hint></v-select>
            </v-flex>
            <v-flex xs10 offset-xs1>
              <v-select label="Select" dense :items="components" v-model="conditon.components" multiple max-height="200" hint="" persistent-hint></v-select>
            </v-flex>
            <v-flex xs10 offset-xs1>
              <v-select label="Select" dense :items="casting_time" v-model="conditon.casting_time" max-height="200" hint="" persistent-hint></v-select>
            </v-flex>
          </v-layout>
        </v-card>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click.native="closeMobileSearchDetailDialog"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>

</template>


<style>
/*table-layout: fixed;  text-overflow: ellipsis; ...
 * http://chuckwebtips.hatenablog.com/entry/2017/07/09/183527 
 */
td.nowrap {
  max-width: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
}
</style>

<script>
import spells from "@/model/spells";
import constants from "@/model/constants";
import Spell from "@/model/spell";

import FileUploadIcon from "@/components/FileUploadIcon";
import FileDownloadIcon from "@/components/FileDownloadIcon";
import SpellDetailDialog from "@/components/SpellDetailDialog";
import SpellHelpDialog from "@/components/SpellHelpDialog";

export default {
  name: "SearchSpellTable",
  components: {
    FileUploadIcon: FileUploadIcon,
    FileDownloadIcon: FileDownloadIcon,
    SpellDetailDialog: SpellDetailDialog,
    SpellHelpDialog: SpellHelpDialog
  },
  mounted() {
    (window.adsbygoogle = window.adsbygoogle || []).push({});
  },
  data() {
    return {
      //
      spelldata: [],
      //
      conditon: {
        spellname: "",
        levels: [],
        classes: [],
        ritual: null,
        components: [],
        casting_time: ""
      },
      //
      snackbar: {
        show: false,
        level: "info",
        message: ""
      },
      //
      levels: constants.levels,
      classes: constants.classes,
      components: constants.components,
      concentration: constants.concentration,
      schools: constants.schools,
      casting_time: constants.casting_time,
      headers: [
        { text: "", value: "name", align: "left", width: "30%" },
        { text: "", value: "level", align: "left", width: "5%" },
        { text: "", value: "components", align: "left", width: "20%" },
        {
          text: "",
          value: "casting_time",
          align: "left",
          width: "15%"
        },
        { text: "", value: "duration", align: "left", width: "10%" },
        { text: "", value: "range", align: "left", width: "15%" },
        { text: " ", value: "dummy", align: "left", width: "5%" }
      ],
      conditonRituals: [
        { text: "", value: null },
        { text: "", value: "yes" },
        { text: "", value: "no" }
      ],
      //
      showEditDialog: false,
      //
      createSpell: false,
      //
      targetSpell: null,
      //
      showHelpDialog: false,
      //
      showMobileSearchDetailDialog: false
    };
  },
  beforeMount() {
    //1.
    var spellsjson = localStorage.getItem("dndapp.spells");
    //2.
    if (spellsjson != undefined && spellsjson != null) {
      //
      this.spelldata = Spell.assigns(JSON.parse(spellsjson));
    } else {
      //2.
      this.spelldata = Spell.assigns(spells());
    }
  },
  computed: {
    //
    items() {
      return (
        this.spelldata
          // .slice(1, 10) //for debug.
          .filter(element => {
            if (!this.filterSpellname(element)) {
              return false;
            }
            if (!this.fliterRitual(element)) {
              return false;
            }
            if (!this.fliterLevels(element)) {
              return false;
            }
            if (!this.fliterClasses(element)) {
              return false;
            }
            if (!this.fliterComopnents(element)) {
              return false;
            }
            if (!this.filterCastingTime(element)) {
              return false;
            }
            return true;
          })
      );
    },
    //
    jesonspelldata() {
      return JSON.stringify(this.spelldata);
    },
    //
    //(/DOMv-ifDOM
    // watch)
    isMobile() {
      const mobileBreakpoints = ["xs", "sm"];
      return mobileBreakpoints.some(e => {
        return this.$vuetify.breakpoint.name === e;
      });
    }
  },

  methods: {
    //computed
    fliterRitual(element) {
      if (this.conditon.ritual === null) {
        return true;
      } else {
        return element.ritual === this.conditon.ritual;
      }
    },
    fliterLevels(element) {
      if (this.conditon.levels.length === 0) {
        return true;
      } else {
        return this.conditon.levels.some(e => {
          return e === element.level;
        });
      }
    },
    fliterClasses(element) {
      if (this.conditon.classes.length === 0) {
        return true;
      } else {
        return this.conditon.classes.some(e => {
          return element.class.includes(e);
        });
      }
    },
    fliterComopnents(element) {
      if (this.conditon.components.length === 0) {
        return true;
      } else {
        if (element.components.length !== this.conditon.components.length) {
          return false;
        }
        for (const s of element.components) {
          if (this.conditon.components.indexOf(s) === -1) {
            return false;
          }
        }
        return true;
      }
    },
    filterSpellname(element) {
      var trimed = this.conditon.spellname.trim();
      if (trimed === "") {
        return true;
      } else {
        //FIXME includes
        return element.name.includes(trimed);
        //return element.name.startsWith(trimed);
      }
    },

    //FIXME constants
    filterCastingTime(element) {
      if (this.conditon.casting_time === null) {
        return true;
      } else {
        if (this.conditon.casting_time !== "") {
          return element.casting_time.startsWith(this.conditon.casting_time);
        } else {
          let result = !(
            element.casting_time.startsWith("1") ||
            element.casting_time.startsWith("1") ||
            element.casting_time.startsWith("1")
          );
          return result;
        }
      }
    },

    //JSON
    onFileRead(files) {
      for (const file of files) {
        let fileReader = new FileReader();
        fileReader.onload = data => {
          let json = data.target.result;
          try {
            let spells = JSON.parse(json);
            this.spelldata = Spell.assigns(spells);
            this.showSnackbar(
              "success",
              ""
            );
          } catch (e) {
            //FIXME
            this.showSnackbar("error", "");
            this.console.log(e);
          }
        };
        fileReader.readAsText(file);
      }
    },
    //expand
    clickCell(item) {
      // props.expanded = !props.expanded;
      this.targetSpell = item;
      this.showEditDialog = true;
    },
    //
    addSpell() {
      this.targetSpell = null;
      this.createSpell = true;
      this.showEditDialog = true;
    },
    // //
    save(result) {
      if (this.targetSpell == null) {
        this.spelldata.push(result);
        this.showSnackbar(
          "success",
          "" + this.result.name + ""
        );
      } else {
        Object.assign(this.targetSpell, result);
        this.targetSpell = null;
        this.showSnackbar(
          "success",
          "" + this.result.name + ""
        );
      }
    },
    // //
    remove() {
      let index = this.spelldata.indexOf(this.targetSpell);
      this.spelldata.splice(index, 1);
      this.targetSpell = null;
      this.showSnackbar(
        "success",
        ""
      );
    },

    // 
    cancelOrClose() {
      this.targetSpell = null;
    },

    showHelp() {
      this.showHelpDialog = true;
    },

    //
    saveSpellData() {
      localStorage.setItem("dndapp.spells", this.jesonspelldata);
      this.showSnackbar(
        "success",
        ""
      );
    },

    //
    loadSpellData() {
      var spellsFromStorage = localStorage.getItem("dndapp.spells");
      //
      if (spellsFromStorage != undefined && spellsFromStorage != null) {
        this.spelldata = Spell.assigns(JSON.parse(spellsFromStorage));
        this.showSnackbar(
          "success",
          ""
        );
      } else {
        this.showSnackbar(
          "error",
          ""
        );
      }
    },

    //
    deleteSpellData() {
      localStorage.removeItem("dndapp.spells");
      this.spelldata = [];
      //this.spelldata = spells(); //
      this.showSnackbar(
        "success",
        ""
      );
    },

    formatSpellName(item) {
      var retVal = item.name + " " + item.format(item.school, this.schools);
      if (item.ritual === "yes") {
        retVal += "";
      }
      retVal += ")";
      return retVal;
    },

    showSnackbar(level, message) {
      this.snackbar.level = level;
      this.snackbar.message = message;
      this.snackbar.show = true;
    },

    closeMobileSearchDetailDialog() {
      this.showMobileSearchDetailDialog = false;
    }
  }
};
</script>



