
/** */
export default class Spell {
  constructor(){
    this.name = "";
    this.desc = "";
    this.higher_level = "";
    this.page = "";
    this.range = "";
    this.components = [];
    this.materialdeteil = ""
    this.ritual = "";
    this.duration = "";
    this.concentration = "";
    this.casting_time = "";
    this.level = "";
    this.school = "";
    this.class = []
  }

  /** */
  get formatDuration(){
    var retVal = this.duration;
    if (this.concentration === "yes") {
      retVal = "," + retVal;
    }
    return retVal;
  }

  /** utils. */
  //val: tablevaluetext
  format(val, table) {
    var retVal = val;
    table.forEach(element => {
      if (element.value === val) {
        retVal = element.text;
        return;
      }
    });
    return retVal;
  }

  //
  formatArray(val, table) {
    var array = [];
    val.forEach(element => {
      array.push(this.format(element, table));
    });
    return array.join(",");
  }

  //**Spells */
  // Spells
  static assigns(array){
    var retVal = [];
    array.forEach(obj => {
      retVal.push(Spell.assign(obj));
    });
    return retVal;
  }

  // Spells
  static assign(obj){
    var spell = new Spell()
    Object.assign(spell,obj);
    return spell;
  }

}



