// import abilityType from './abilityType'
export default class Constants {
  static get classes() {
    return [
      { text: "", value: "Bard" },
      { text: "", value: "Cleric" },
      { text: "", value: "Druid" },
      { text: "", value: "Paladin" },
      { text: "", value: "Ranger" },
      { text: "", value: "Sorcerer" },
      { text: "", value: "Wizard" },
      { text: "", value: "Warlock" }
    ];
  }

  static get rituals() {
    return [
      { text: "", value: "yes" },
      { text: "", value: "no" }
    ];
  }

  static get components() {
    return [
      { text: "", value: "S" },
      { text: "", value: "V" },
      { text: "", value: "M" }
    ];
  }

  static get concentration() {
    return [
      { text: "", value: "yes" }, 
      { text: "", value: "no" }
    ];
  }

  static get schools() {
    return [
      { text: "", value: "Illusion" }, 
      { text: "", value: "Conjuration" },
      { text: "", value: "Necromancy" },
      { text: "", value: "Enchantment" },
      { text: "", value: "Divination" },
      { text: "", value: "Transmutation" },
      { text: "", value: "Abjuration" },
      { text: "", value: "Evocation" },
    ];
  }

  static get casting_time() {
    return [
      { text: "", value: null },
      { text: "1", value: "1" },
      { text: "1", value: "1" }, 
      { text: "1", value: "1" }, 
      { text: "", value: "" }, 
    ];
  }

  static get levels() {
    return [
      "Cantrip",
      "1st-level",
      "2nd-level",
      "3rd-level",
      "4th-level",
      "5th-level",
      "6th-level",
      "7th-level",
      "8th-level",
      "9th-level"
    ]
  }
}



